.books-section {
  background: var(--body-bg-light);
  background: #b0e0e6;
}

.top-category-item {
  margin-bottom: 40px;
}

.name-top-category {
  color: var(--top-category-title);
  letter-spacing: .42px;
  text-transform: uppercase;
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.books-container {
  margin-bottom: 8px;
}

.book-card {
  width: 100%;
  margin: 0 auto;
}

.book-card-img {
  height: 485px;
  width: 100%;
  margin-bottom: 14px;
  display: block;
}

.book-card-title {
  color: var(--text-color);
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.13;
}

.book-card-author {
  color: var(--top-category-title);
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
}

.see-more-btn {
  color: var(--text-color);
  letter-spacing: -.14px;
  text-transform: uppercase;
  border: 2px solid var(--violet);
  border-radius: 40px;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
}

.btn-container-top-books {
  justify-content: flex-end;
  display: flex;
}

@media screen and (min-width: 768px) {
  .book-card {
    width: calc(33.3333% - 16.6667px);
  }

  .books-container {
    gap: 25px;
    margin-bottom: 20px;
    display: flex;
  }

  .book-card-img {
    height: 316px;
    display: block;
  }
}

@media screen and (min-width: 1440px) {
  .book-card {
    width: calc(20% - 19.2px);
  }

  .books-container {
    gap: 24px;
    margin-bottom: 32px;
  }
}

